import React, { useState, useEffect } from "react"
import { Location } from "@reach/router"
import Axios from "../../axios"
import { BASE_URL } from "../../config"
import ReactMarkdown from "react-markdown"
import Header from "../components/header"

export default function NewsInfo({ id }) {
  const [News, setNews] = useState({})
  useEffect(() => {
    async function fetchData() {
      const request = await Axios.get(`/newes/${id}`)

      setNews(request.data)
      return request
    }
    fetchData()
  }, [])
  return (
    <div>
      <Header isHomePage={false} title="News & Events" />
      <h2>{News?.title}</h2>
      <h2>{News?.date}</h2>
      <img src={`${BASE_URL}${News?.img?.url}`} className="img-fluid" />
      <ReactMarkdown
        source={News.description}
        transformImageUri={uri =>
          uri.startsWith("http") ? uri : `${BASE_URL}${uri}`
        }
      />
    </div>
  )
}
